<style lang="stylus">
.comp-answer
  position relative
  >header
  	background-color #f71084
  >.content
  	background #fff
  	.sbtn
  		margin auto
</style>

<template lang="pug">
.comp-answer.comp-index
  header
    img.logo(src="../assets/pc/logo-2.jpg")
  .content
    img.tips(v-if="type === 0", src="../assets/tips-done.png")
    img.tips(v-else, src="../assets/tips-none.png")
    .sbtn(@click="redirect('compIndex')") 返回
</template>

<script>
import utils from 'lib/utils'
export default {
	components: {},
	props: {
		param: Object
	},
	data () {
		return {}
	},
	computed: {
		type () {
			return (this.param && this.param.type) || 0
		}
	},
	methods: {
    redirect (page) {
      utils.redirect(page)
    }
  },
	mounted () {}
}
</script>



