import utils from 'lib/utils'

export function sendPush(src, callback) {
	var errMsg = checkSrc1(src)
	if (errMsg) {
		utils.alert(errMsg)
		return
	}
	utils.ajax({
		url: 'index.php?r=push%2Fadd&' + getData(src),
		type: 'GET',
		dataType: 'jsonp'
	}).then(() => {
		callback && callback()
	}).catch((data) => {
		errMsg = data && data.msg ? data.msg : '网络返回异常'
		utils.alert(errMsg)
	})
}

function checkSrc1(src) {
	var errMsg = ''
	if (!src.name || src.name.length < 2) {
		errMsg = '姓名有误'
	} else if (!src.department) {
		errMsg = '部门有误'
	} else if (!src.guy_name || src.guy_name.length < 2) {
		errMsg = '推荐人姓名有误'
	} else if (!src.guy_phone || src.guy_phone.length != 11) {
		errMsg = '推荐人手机有误'
	} else if (!src.guy_email || !utils.isEmail(src.guy_email)) {
		errMsg = '推荐人邮箱有误'
	}
	return errMsg
}

function checkSrc2(src) {
  var errMsg = ''
  if (!src.name || src.name.length < 2) {
    errMsg = '姓名有误'
  } else if (!src.guy_name || src.guy_name.length < 2) {
    errMsg = '推荐人姓名有误'
  } else if (!src.guy_phone || src.guy_phone.length != 11) {
    errMsg = '推荐人手机有误'
  }
  return errMsg
}

function getData(src) {
	var data = ''
	for (var key in src) {
		data += '&{key}={value}'.replace('{key}', getDataKey(key)).replace('{value}', src[key])
	}
	return data.substr(1)
}

function getDataKey(key) {
	return 'PushRecord[{key}]'.replace('{key}', key)
}

export function checkOne(src, callback) {
	var errMsg = checkSrc2(src)
	if (errMsg) {
		utils.alert(errMsg)
		return
	}
	utils.ajax({
		url: 'index.php?r=push%2Fone&' + getData(src),
		type: 'GET',
		dataType: 'jsonp'
	}).then((data) => {
		callback && callback(data)
	}).catch((data) => {
		errMsg = data && data.msg ? data.msg : '网络返回异常'
		utils.alert(errMsg)
	})
}



