<style lang="stylus">
.main-page
  .content
    min-height 300px
  .answer
    width 80%
    height 80%
    margin 75px auto
    color #666
</style>

<template lang="jade">
.main-page
  header.header
    img(src="../assets/bg-3.jpg")
  .content(v-if="!isAnswer")
    input.input(type="text", v-model="push.name", placeholder="您的姓名")
    input.input(type="text", v-model="push.guy_name", placeholder="您推荐的学生姓名")
    input.input(type="text", v-model="push.guy_phone", placeholder="他/她的手机号码", maxlength="11")
    .sbtn(@click="sendPush") 查询
    p.tips 查询我的内推记录
    p.check-record.sred(@click="redirect('main-page')") 返回
  .content(v-else)
    p.answer(v-if="!hasRecord")
      | 此学生尚未推荐哦~
      br
      | 请点击返回，提交其相关信息
    p.answer(v-else)
      | 抱歉，您已推荐过{{push.guy_name}}，
      br
      | 请勿重复操作，谢谢！
      br
      | 内推信息将在各阶段结束之后反馈给您，
      br
      | 请注意查收！
    .sbtn(@click="isAnswer = false") 返回
</template>

<script>
import $ from 'jquery'
import utils from 'lib/utils'
import {checkOne} from 'lib/api'
export default {
  data () {
    return {
      push: {},
      isAnswer: false,
      hasRecord: true
    }
  },
  methods: {
    sendPush () {
      var vm = this
      var src = vm.push
    	checkOne(src, (data) => {
        vm.isAnswer = true
        vm.hasRecord = !(data instanceof Array)
    	})
    },
    redirect (page) {
      utils.redirect(page)
    }
  }
}
</script>





