<style lang="stylus">
.compInput
	main-width = 300PX
	position absolute
	top 45%
	right 50PX
	transform translateY(-50%)
	width main-width
	overflow hidden
	border-radius 8PX
	background-color rgba(255, 255, 255, 0.6)
	.tabs
		color #fff
		font-size 18PX
		line-height 50PX
		background-color #f90f85
		.tab
			display inline-block
			position relative
			width 50%
			text-align center
			cursor pointer
			&.active
				&:after
					content ''
					position absolute
					bottom 0
					left 50%
					transform translateX(-50%)
					width main-width * 0.5 - 50
					height 4PX
					background-color #fff
			&:first-child
				&:before
					content ''
					position absolute
					top 50%
					right 0
					transform translateY(-50%)
					width 1PX
					height 35PX
					background-color #fff
	.content
		padding-bottom 30PX
		.group
			width main-width - 50
			margin auto
			margin-top 20PX
			padding 5PX 8PX
			border 1PX solid #fff
			border-radius 5PX
			>i, >input
				float left
			>input
				width main-width - 50 - 30
				margin-left 5PX
				line-height 20PX
				text-align center
				background-color transparent
				border none
				ph-color = #fff
				&::-webkit-input-placeholder
					color ph-color
				&:-moz-placeholder
					color ph-color
				&:-ms-input-placeholder
					color ph-color
		.option
			text-align center
			.btn-right
				font-size 30PX
				opacity 0
				&.active
					opacity 1
			.sbtn
				width main-width - 50
				margin auto
				font-size 12PX
			.tips
				margin-top 5PX
				color #666
				font-size 12PX
	.icon
		display inline-block
		width 20PX
		height 20PX
		background-size contain
	.icon1
		background-image url('../assets/icon/1.png')
	.icon2
		background-image url('../assets/icon/2.png')
	.icon3
		background-image url('../assets/icon/3.png')
	.icon4
		background-image url('../assets/icon/4.png')
	.icon5
		background-image url('../assets/icon/5.png')
</style>

<template lang="pug">
.compInput
	header.tabs
		span.tab(:class="{active: status === 0}", @click="tabSelect(0)") 我要内推
		span.tab(:class="{active: status === 1}", @click="tabSelect(1)") 我要查询
	.content(v-if="status === 0")
		.group.clearfix
			i.icon.icon1
			input(type="text", v-model="name", placeholder="您的姓名")
		.group.clearfix
			i.icon.icon2
			input(type="text", v-model="department", placeholder="您的部门(精确到最末端部门)")
		.group.clearfix
			i.icon.icon3
			input(type="text", v-model="guy_name", placeholder="您推荐的学生姓名")
		.group.clearfix
			i.icon.icon4
			input(type="text", v-model="guy_phone", placeholder="TA的手机号", maxlength="11")
		.group.clearfix
			i.icon.icon5
			input(type="text", v-model="guy_email", placeholder="TA的邮箱")
		.option
			i.btn-right(:class="{active: isShowSucc}")
			.sbtn(@click="sendPush") 提交
			p.tips 提交后可继续填写您推荐的下一个牛人
	.content(v-else)
		.group.clearfix
			i.icon.icon1
			input(type="text", v-model="name", placeholder="您的姓名")
		.group.clearfix
			i.icon.icon3
			input(type="text", v-model="guy_name", placeholder="您推荐的学生姓名")
		.group.clearfix
			i.icon.icon4
			input(type="text", v-model="guy_phone", placeholder="TA的手机号", maxlength="11")
		.option
			i.btn-right(:class="{active: isShowSucc}")
			.sbtn(@click="checkOne") 查询
			p.tips 查询内推记录
</template>

<script>
import utils from 'lib/utils'
import {sendPush, checkOne} from 'lib/api'

export default {
	components: {},
	data () {
		return {
	    name: '',
	    department: '',
	    guy_name: '',
	    guy_phone: '',
	    guy_email: '',
			isShowSucc: false,
			status: 0
		}
	},
	computed: {},
	methods: {
    sendPush () {
    	sendPush({
        name: this.name,
        department: this.department,
        guy_name: this.guy_name,
        guy_phone: this.guy_phone,
        guy_email: this.guy_email
    	}, () => {
        this.guy_name = ''
        this.guy_phone = ''
        this.guy_email = ''
        utils.alert('提交成功', true)
        this.isShowSucc = true
        setTimeout(() => {
        	this.isShowSucc = false
        }, 500)
    	})
    },
    checkOne () {
    	checkOne({
    		name: this.name,
    		guy_name: this.guy_name,
    		guy_phone: this.guy_phone
    	}, (data) => {
    		let hasRecord = !(data instanceof Array)
	      utils.redirect('compAnswer', {
					type: hasRecord ? 0 : 1
				})
    	})
    },
    tabSelect (index) {
    	this.status = index
    }
  },
	mounted () {}
}
</script>



